// app/my/page.tsx
import { redirect } from 'next/navigation'
import { cookies } from 'next/headers'
import { createServerComponentClient } from '@supabase/auth-helpers-nextjs'

type Row = { id: string; image_path: string; description: string; created_at: string }

export default async function MySubmissionsPage() {
  const supabase = createServerComponentClient({ cookies })
  const { data: { user } } = await supabase.auth.getUser()
  if (!user) redirect('/')

  // Fetch rows and normalize null -> []
  const { data, error } = await supabase
    .from('submissions')
    .select('id,image_path,description,created_at')
    .order('created_at', { ascending: false })
    .returns<Row[]>() // helps TS know the shape

  if (error) {
    return (
      <main className="p-6">
        <h1 className="text-xl font-bold">My Submissions</h1>
        <p className="text-red-600 mt-2">Error: {error.message}</p>
      </main>
    )
  }

  const rows: Row[] = data ?? []

  // Signed URLs (normalize to [])
  const paths = rows.map(r => r.image_path)
  let signed: { path: string; signedUrl?: string }[] = []
  if (paths.length) {
    const { data: signedData } = await supabase.storage.from('photos').createSignedUrls(paths, 3600)
    signed = signedData ?? []
  }
  const urlMap = new Map(signed.map(s => [s.path, s.signedUrl ?? '']))

  return (
    <main className="p-6 grid gap-4">
      <h1 className="text-xl font-bold">My Submissions</h1>
      {rows.length ? rows.map(r => (
        <article key={r.id} className="border rounded-xl p-4 grid gap-2">
          {urlMap.get(r.image_path) && (
            <img
              src={urlMap.get(r.image_path)!}
              alt="submission"
              className="w-full rounded-lg"
            />
          )}
          <p className="whitespace-pre-wrap">{r.description}</p>
          <time className="text-sm opacity-70">
            {new Date(r.created_at).toLocaleString()}
          </time>
        </article>
      )) : (
        <p>No submissions yet.</p>
      )}
    </main>
  )
}
